.form__input {
    color: #000;
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: calc(16.94/14);
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    padding-bottom: 8px;
    padding-left: 0;
    box-sizing: border-box;
}

@media screen and (min-width: 768px) {
    .form__input {
        padding-bottom: 8px;
        padding-top: 5px;
    }
}