.profile__edit-button {
    background-image: url('../../../images/edit-button.svg');
    background-size: cover;
    border: none;
    width: 18px;
    height: 18px;
    background-color: #000;
    align-self: center;
    cursor: pointer;
    transition: opacity 1s ease;
}

.profile__edit-button:hover {
    opacity: .6;
}

@media screen and (min-width: 1024px) {
    .profile__edit-button {
        width: 24px;
        height: 24px;
    }
}