.footer__copyright {
    margin: 0;
    color: #545454;
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: calc(16.94/14);
}

@media screen and (min-width: 1280px) {
    .footer__copyright {
        font-size: 18px;
        line-height: calc(21.78/14);
    }
}